<template>
  <div class="coupons">
    <van-nav-bar
      title="我的优惠券"
      fixed
      @click-left="$toPage(-1)"
      @click-right="$toPage('toCoupons')"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
      <span slot="right" style="font-size: 15px">领券中心</span>
    </van-nav-bar>
    <van-tabs @click="onClick">
      <van-tab title="可使用" animated>
        <conpous :coupons="coupons" />
        <div v-if="flag" class="bgImg"></div>
      </van-tab>
      <van-tab title="已使用">
        <conpous :coupons="coupons" />
        <div v-if="flag" class="bgImg"></div>
      </van-tab>
      <van-tab title="已过期">
        <conpous :coupons="coupons" />
        <div v-if="flag" class="bgImg"></div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import conpous from '@/components/couponsAssembly.vue'
export default {
  data() {
    return {
      coupons: [],
      count: 0,
      flag: true,
    };
  },
  components: {
    conpous
  },
  mounted() {
    if((this.$loadUserInfo().comp.authenticationStatus & 256) > 0){
      this.getCoupons()
    }
  },
  methods: {
    //  获取优惠券
    getCoupons() {
      this.coupons = []
      this.flag = true
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "CouponRevModule",
        method: "queryRevCouponList",
        params: {
          compid: this.$loadUserInfo().comp.storeId,
          type: this.count,
          pageSize: 100,
          pageNo: 1
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            this.coupons = result.data

            if(result.data.length > 0){
              this.flag = false
            }
            this.coupons.forEach( (data) => {
              if(this.count === 0){
                data.btn = '立即使用'
              }else if(this.count === 1){
                data.btn = '已使用'
              }else {
                data.btn = '已过期'
              }
              if(data.brulecode === 2110){
                data.reduce = '减'
                data.symbol = '元'
              }else if(data.brulecode === 2120) {
                data.reduce = '包邮'
                // if(data.ladderVOS[0].offer === 0) {
                data.ladderVOS[0].offer = ''
                // }
              }else {
                data.reduce = ''
                data.symbol = '折'
              }
            })
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    onClick(index) {
      this.count = index
      if((this.$loadUserInfo().comp.authenticationStatus & 256) > 0){
        this.getCoupons()
      }
    },
  }
};
</script>
<style scoped>
.coupons .van-nav-bar{
  z-index: 999;
}
.coupons .van-tabs{
  margin-top: 92px;
  z-index: 0;
}
.coupons .bgImg{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80vh;
  background: url('~@\assets\noneImg\youhui.png') no-repeat;
  background-position: center center;
  background-size: 50%;
}
.coupons .van-tabs .van-hairline--top-bottom {
  position: fixed;
  top: 0;
  left: 0;
}
</style>
