<template>
  <div class="coupons">
    <div class="main" v-for="(coupon, index) in coupons" :key="index">
      <div class="title">
        {{ coupon.rulename }}
      </div>
      <div class="content">
        <div class="couponsText">
          <p v-for="(item, index) in coupon.ladderVOS" :key="index">
            满{{ item.ladamt }}元{{ coupon.reduce }}{{ item.offer }}{{ coupon.symbol }}
          </p>
          <p v-if="!coupon.validday" class="time">{{ coupon.startdate }}~{{ coupon.enddate }}</p>
          <p v-if="coupon.validday" class="time">有效天数：{{ coupon.validday }}天</p>
        </div>
        <div class="btn" @click="receive(coupon,index)">
          {{ coupon.btn }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['coupons'],
  data() {
    return {
    }
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  methods: {
    receive(coupon,i) {
      if (coupon.btn === '立即领取') {
        JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CouponRevModule",
        method: "revCoupon",
        params: {
          coupno: coupon.coupno,
          compid: coupon.compid,
          rulename: coupon.rulename,
          brulecode: coupon.brulecode,
          validday: coupon.validday,
          validflag: coupon.validflag,
          glbno: coupon.glbno,
          goods: coupon.goods,
          qlfno: coupon.qlfno,
          qlfval: coupon.qlfval,
          ladderVOS: coupon.ladderVOS,
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            this.$emit('couponBtn',false)
            coupon.btn = '已领取'
            this.$toast(result.data);
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
      }else if(coupon.btn === '立即使用'){
        this.$router.push({
          path: '/shoppingCart',
          query: {
            cart: true
          }
        })
      }
    }
  }
}
</script>
<style scoped>
.coupons .main {
  width: 715px;
  height: 152px;
  overflow: hidden;
  margin: 20px auto;
}
.coupons .main .title {
  width: 230px;
  height: 152px;
  background: url("../assets/me_icon/coupon.png") no-repeat;
  background-size: contain;
  font-size: 32px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  line-height: 152px;
  float: left;
}
.coupons .main .content {
  width: 480px;
  height: 148px;
  border: 1px solid rgba(230, 230, 230, 1);
  border-left: none;
  float: left;
}
.coupons .main .content .couponsText {
  font-size: 28px;
  width: 330px;
  height: 100%;
  float: left;
  margin: 10px 0;
  position: relative;
}
.coupons .main .content .couponsText p {
  margin: 10px 0 0 30px;
}
.coupons .main .content .couponsText .time {
  position: absolute;
  bottom: 20px;
  font-size: 20px;
  color: rgba(153, 153, 153, 1);
}
.coupons .main .content .btn {
  width: 150px;
  height: 152px;
  float: left;
  text-align: center;
  line-height: 152px;
  font-size: 28px;
  color: rgba(248, 134, 57, 1);
}

</style>